<template>
  <div class="join">
    <div class="join_cont">
      <h3>회원가입</h3>
      <ValidationObserver ref="observer" tag="div" v-slot="{ invalid }">
        <p><span class="circle">●</span> 이름</p>
        <InputWithValidation rules="required|name"
                             type="text"
                             ref="name"
                             title="이름"
                             name="form.name"
                             v-model="form.name"
                             autocomplete="off"
                             placeholder="이름을 입력하세요."/>
        <p><span class="circle">●</span> 이메일</p>
        <InputWithValidation rules="required|email"
                             type="text"
                             ref="email"
                             title="이메일"
                             name="form.email"
                             v-model="form.email"
                             autocomplete="off"
                             placeholder="이메일을 입력해주세요."/>
        <p><span class="circle">●</span> 아이디</p>
        <InputWithValidation rules="required|id"
                             type="text"
                             ref="id"
                             title="아이디"
                             name="form.userId"
                             v-model="form.userId"
                             autocomplete="off"
                             placeholder="아이디 (대소문자/숫자 (2~14자리))"/>
        <p><span class="circle">●</span> 비밀번호</p>
        <InputWithValidation rules="required|password"
                             type="password"
                             ref="pw"
                             title="비밀번호"
                             name="form.password"
                             v-model="form.password"
                             autocomplete="off"
                             placeholder="비밀번호 (대소문자/숫자/특수문자 (10~16자리))"/>
        <p><span class="circle">●</span> 닉네임</p>
        <InputWithValidation rules="required|nickname"
                             type="text"
                             ref="nickname"
                             title="닉네임"
                             name="form.nickname"
                             v-model="form.nickname"
                             autocomplete="off"
                             placeholder="닉네임 (한글/대소문자/숫자 (2~8자리)"/>


        <div class="agreement_wrap">
          <div class="agreement">
            <ul>
              <li>
                <CheckWithValidation rules="required"
                                     lable="만 14세 이상입니다."
                                     title="만 나이"
                                     v-model="ageAgree"
                                     :options="textAgeAgree" />
              </li>

              <li>
                <CheckWithValidation rules="required"
                                     lable="이용약관동의"
                                     title="이용약관 동의"
                                     v-model="personalAgree"
                                     :options="textPersonalAgree" />
                <span v-b-modal.modal-agree class="text_show">보기</span>
              </li>

              <li>
                <CheckWithValidation rules="required"
                                     lable="개인정보수집"
                                     title="개인정보 수집"
                                     v-model="locationAgree"
                                     :options="textLocationAgree" />
                <span v-b-modal.modal-personal-agree class="text_show">보기</span>
              </li>

              <li>
                <CheckWithValidation rules=""
                                     title="마케팅 정보"
                                     v-model="marketingAgree"
                                     :options="textMarketingAgree" />
                <span v-b-modal.modal-marketing-agree class="text_show">보기</span>
              </li>
            </ul>
          </div><!-- agreement -->
        </div><!--agreement_wrap  -->

        <div class="btn_wrap">
          <ul>
            <li class="mr5">
              <router-link to="/login">
                <button class="button_default_border">취소</button>
              </router-link>
            </li>
            <li>
              <button class="button_default" @click="onSubmit" :disabled="invalid">회원가입</button>
            </li>
          </ul>
        </div>
      </ValidationObserver>

      <ModalAgree/>
      <ModalPersonalAgree/>
      <ModalMarketingAgree/>
    </div><!-- join_cont -->
  </div>
</template>

<script>
import ModalAgree from "@/components/modal/ModalAgree.vue";
import ModalPersonalAgree from "@/components/modal/ModalPersonalAgree.vue";
import ModalMarketingAgree from "@/components/modal/ModalMarketingAgree.vue";
import {ValidationObserver} from 'vee-validate'
import InputWithValidation from '@/components/common/validations/inputbox'
import CheckWithValidation from '@/components/common/validations/checkbox'
import ax from '@/api/myPage'

export default {
  name: "Join",
  components: {
    ModalAgree,
    ModalPersonalAgree,
    ModalMarketingAgree,
    ValidationObserver,
    InputWithValidation,
    CheckWithValidation
  },
  data() {
    return {
      totalAgreeCheck: false,
      personalAgree: false,
      locationAgree: false,
      ageAgree: false,
      marketingAgree: false,
      form: {
        name: '',
        email: '',
        userId: '',
        password: '',
        nickname: ''
      },
      textTotalAgree: [{value: 'true', text: '전체동의'}],
      textAgeAgree: [{value: 'true', text: '만 14세 이상입니다. (필수)'}],
      textPersonalAgree: [{value: 'true', text: '이용약관 동의 (필수)'}],
      textMarketingAgree: [{value: 'true', text: '마케팅 정보 수진 동의 (선택)'}],
      textLocationAgree: [{value: 'true', text: '개인정보 수집 및 이용 동의 (필수)'}],
    }
  },
  methods: {
    onSubmit() {
      ax.signUp(this.form, (response) => {
        if (response.data.success) {
          alert('회원가입을 축하드립니다.')
          this.$router.push('/login')
        }
      })
    },

  }
}
</script>

<style>
[data-v-651aa4dc].custom_common_form-group .custom-control-label {
  padding-top: 1px;
}
</style>
