<template>
  <ValidationProvider :name="($attrs.title ? $attrs.title : $attrs.label)"
                      :rules="rules"
                      v-slot="{ valid, errors }">
    <b-form-group v-bind="$attrs"
                  class="custom_common_form-group"
                  :invalid-feedback="errors[0]"

                  :state="errors[0] ? false : null">
      <b-form-checkbox-group
        :state="errors[0] ? false : null"
        v-model="innerValue"
        :options="options"
>
      </b-form-checkbox-group>
    </b-form-group>
  </ValidationProvider>
</template>

<script>
import {ValidationProvider} from 'vee-validate'

export default {
  components: {
    ValidationProvider
  },
  props: {
    vid: {
      type: String
    },
    rules: {
      type: [Object, String],
      default: ''
    },
    options: {
      type: [Object, Array],
      default: function () {
        return []
      }
    },
    // must be included in props
    value: {
      type: null
    }
  },
  data: () => ({
    innerValue: []
  }),
  watch: {
    innerValue(newVal) {
      this.$emit('input', newVal)
    },
    value(newVal) {
      this.innerValue = newVal
    }
  },
  created() {

    if (this.value) {
      this.innerValue = this.value
    }
  },
  methods: {
    test() {
      this.checked = !this.checked
      this.$emit('checkBoolean', this.checked)
    }
  }
}
</script>

<style scoped>
::v-deep.custom_common_form-group .custom-control-label {
  padding-top: 5px;
}
</style>
