import axios from '@/api/axios'

const signUp = async (formData, result) => {
  const options = {
    method: 'POST',
    url: '/user/signup',
    data: formData
  }
  await axios(options)
    .then((response) => {
      if (response.data.success) {
        result(response.data.data)
      } else {
        result(null)
      }
    })
    .catch((error) => {
      if (error.isAxiosError) {
        alert('이미 가입된 아이디입니다.')
      }
 /*     if (response.response.data.error.statusCode === 409) {
        alert('이미 가입된 아이디입니다.')
      }*/
    })
}

const login = async (formData, result) => {
  const options = {
    method: 'POST',
    url: '/user/login',
    data: formData
  }
  await axios(options)
    .then((response) => {
      if (response.data.success) {
        result(response.data)
      } else {
        result(null)
      }
    })
    .catch((error) => {
      if (error.isAxiosError) {
        alert('아이디 비밀번호를 다시 확인해주세요')
      } else {
        console.log('no')
      }
      /* if (response.response.data.error.statusCode === 401) {
         alert('아이디 비밀번호를 다시 확인해주세요')
       }*/
    })
}


export default {
  signUp,
  login
}